

















































li {
	> a {
		display: flex !important;
		align-items: center;
		position: relative;
		img {
			height: 1em;
			width: 1em;
			margin-right: 0.5em;
			display: inline-block;
		}
		span {
			display: inline-block;
		}
		.delete {
			align-self: flex-end;
			position: absolute;
			right: 0.75rem;
			display: none;
		}
		&:hover .delete {
			display: inline-block;
		}
	}
}
